html, body {
  height : 100%;
  background :#e7fdff;
  background: -webkit-linear-gradient(to right, #e7fdff, #b3faff);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#e7fdff), to(#b3faff));
  background: -webkit-linear-gradient(left, #e7fdff, #b3faff);
  background: -o-linear-gradient(left, #e7fdff, #b3faff);
  background: linear-gradient(to right, #e7fdff, #b3faff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.input-container {
  margin-top: 60px;
}

.input-container .btn-nav{
  width: 100px;
  margin: 10px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}


.vertical-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.horizontal-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack : center;
      justify-content : center;
}

.custom-display-inline-block {
  display: inline-block;
}

.list-element {
  width: 60px;
  height: 60px;
  margin: 10px;
  border-radius: 50%;
}

.list-element h4 {
  margin: 0px;
  font-size: 20px;
  padding: 0px;
}

.triangle-container {
  margin: 50px 0px;
}

.custom-row-reverse {
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
}

.custom-column-reverse {
  -ms-flex-direction : column-reverse;
      flex-direction : column-reverse;
}

.custom-display-flex {
  display: -ms-flexbox;
  display: flex;
}

.custom-row-reverse .big-list-container {
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.custom-row {
  -ms-flex-direction: row;
      flex-direction: row;
}

.custom-row .big-list-container{
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.custom-column {
  -ms-flex-direction : column;
      flex-direction : column;
}

.custom-column .big-list-container,
.custom-column-reverse .big-list-container {
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

